.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  background-color: white;
  height: 78px;
  -webkit-box-shadow: 0 1px 2px 0 rgba(0,0,0,.21);
          box-shadow: 0 1px 2px 0 rgba(0,0,0,.21);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.header__content {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 20px;
  width: 980px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.header__logo {
  margin: 0;
  padding: 0;
}
.header__logo .icon-logo_domicilios {
  font-size: 30px;
  color: #ec3345;
}
.header__btns {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 230px;
}
.header__btns a.icon-search {
  color: black;
  font-size: 24px;
  font-weight: 700;
}
.header__social {
  width: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  align-items: center;
}
.header__social a {
  color: black;
  font-weight: 500;
}
.header__mobile {
  display: none;
}
.header__mobile a {
  margin-left: 15px;
  font-size: 28px;
  color: black;
  font-weight: 500;
}
/*menu mobile*/
.hideClose {
  display: none;
}
.showMenu {
  left: 0 !important;
}
@media only screen and (max-width : 800px) {
  .header {
    height: 61px;
    -webkit-transition: all .2s ease;
    -o-transition: all .2s ease;
    transition: all .2s ease;
  }
  .header__logo .icon-logo_domicilios {
    font-size: 24px;
  }
  .header__mobile {
    display: block;
  }
  .header__btns {
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
    position: fixed;
    top: 61px;
    left:-100vw;
    background-color: white;
    border-top: 1px solid black;
    height: 100vh;
    width: 100vw;
    -webkit-transition: all .2s ease;
    -o-transition: all .2s ease;
    transition: all .2s ease;
  }
  .header__btns a.icon-search {
    display: none;
  }
  .header__btns .smallBtn {
    width: 100%;
    height: 54px;
    font-size: 17px;
    background: white;
    color: #ec3345;
    border-bottom: 1px solid black;
    margin-bottom: 10px;
  }
  .header__social {
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 40px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    width: 90%;
  }
  .header__social a{
    font-size: 20px;
  }
}



