}
@media only screen and (max-width : 800px) {
  .input {
    height: 40px;
  }
  .section {
    min-height: 100vh
  }
  .modal__logo {
    -ms-flex-item-align: start;
        align-self: flex-start;
  }
  .cupon__modal {
    width: 95%;
  }
}
@media only screen and (min-width : 1600px) {
  .section {
    min-height: 800px;
  }
}



