    height: auto;
    width: 100%;
    background-position: center;
  }
  .video__box {
    /*margin-top: 30px;
 		height: 0;
 		overflow: hidden;
 		padding-bottom: 56.25%;
 		padding-top: 30px;
 		position: relative;*/
  }
  .video{
    width: 100%;
    height: 100%;
    /*top: 0;
    left: 0;
    position: absolute;*/
  }
  .contentLoading {
