          box-sizing: border-box;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.login .input {
  width: 300px !important;
  margin-bottom: 20px;
  background: #ece7e7 !important
}
.login a {
  color: #ef3040 !important;
  border-color: #ef3040 !important;
}
.login i {
  color: #ef3040 !important;
  font-size: 30px;
}



